import React from "react";
import "./styles/calculator.css";

function Calculator() {
  return (
    <div className="calculator-body">
      <font style={{ fontSize: "17px" }}>Calculator</font>
      <textarea name="calculation" id="calculation" rows="4"></textarea>
      <div className="calc-btns">
        <button className="clac-btn">9</button>
        <button className="clac-btn">8</button>
        <button className="clac-btn">7</button>
        <button className="clac-btn">+</button>
        <button className="clac-btn">6</button>
        <button className="clac-btn">5</button>
        <button className="clac-btn">4</button>
        <button className="clac-btn">-</button>
        <button className="clac-btn">3</button>
        <button className="clac-btn">2</button>
        <button className="clac-btn">1</button>
        <button className="clac-btn">x</button>
        <button className="clac-btn">.</button>
        <button className="clac-btn">0</button>
        <button className="clac-btn"> = </button>
        <button className="clac-btn">/</button>
      </div>
    </div>
  );
}

export default Calculator;
